














































































































import uniDrawer from '../../../components/uni-drawer.vue';
import uniIcon from '../../../components/uni-icon.vue';
export default {
	components: {
		uniDrawer,
		uniIcon
	},
	data() {
		return {
			rightDrawerVisible: false,
			listData: {},
			nextPage:"",
			page: 1,
			reLoad:true
		}
	},
	methods: {
		closeRightDrawer() {
			this.rightDrawerVisible = false;
		},
		getList() {
			uni.showToast({
				icon:'loading',
				title: '',
				mask: false,
				duration: 500
			});
			let url= 'https://api.thinker.ink/v1/books/';
			if (!this.reLoad){
				
				url=this.nextPage; 
				if(!url){
					uni.showToast({
						icon:'none',
						title: '无更多数据',
						mask: false,
						duration: 1500
					});
					return;
				}
			}
			console.log(url);
			uni.request({
				url: url,
				method: 'GET',
				data: {},
				success: res => {
					console.log(res);
					if (!this.reLoad) {
						res.data.data.forEach((news) => {
							this.listData.push(news);
							console.log(this.listData);
						});
					}else{
					this.listData= res.data.data;
					}
					this.nextPage=res.data.next;
					this.reLoad=false;
					console.log(this.nextPage);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		confirm() {
			uni.showToast({
				title: '搜索'
			})
		}
	},
	onNavigationBarButtonTap(e) {
		console.log("tab顶部键"+e.index);
		switch (e.index){
			case 2:
				console.log("点了评分");
				break;
			case 1:
				break;
			case 0:
			this.rightDrawerVisible = !this.rightDrawerVisible;
				break;
			default:
				break;
		}
		
	},
	onBackPress() {
		// 返回按钮监听
		if (this.rightDrawerVisible) {
			this.rightDrawerVisible = false;
			return true;
		}
	},
	onLoad() {
		this.getList();
	},
	onReachBottom() {
		this.reLoad=false;
		this.getList();
		console.log('bottom');
	},
	onPullDownRefresh() {
		this.reLoad=true;
		this.getList();
		uni.stopPullDownRefresh();
	}
}
